.Mymusic{
  
}
.Mymusic section{
    max-width: 900px;
    margin: 0 auto;
}
.Mymusic .loading{
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
}


/* media query=================================================== */
