/*before reaches smartphones res*/
@media (max-width: 590px){
    header h1{
        font-size: 46px;
    }
    header p{
        font-size: 18px;
    }
    section h1{
        margin-left: 0px;
        margin-right: 0px;
    }
    section{
        padding: 0 10px;
    }
}
